<template>
    <div class="Form tra cứu danh sách các đầu đơn xin cấp phát">
        <el-row :gutter="24" justify="end">
            <el-col :md="4">
                <el-input v-model="search" :placeholder="$t('Đầu đơn/Mã thẻ/Tên')"></el-input>
            </el-col>
            <el-col :md="3">
                <el-button @click="SearchData">{{ $t("Tìm kiếm") }}</el-button>
            </el-col>
        </el-row>
        <br>
        <el-table :data="tableData" :key="tableKey"
        v-loading="Loading"
        :element-loading-text="$t('Đang tải')"
        element-loading-spinner="el-icon-loading" 
        element-loading-background="rgba(0, 0, 0, 0.8)"
        >
            <el-table-column :label="$t('Mã đầu đơn')" fixed  width="160">
                <template #default="scope">
                    <el-button size="small" :link="true" @click="GotoDetailDocNumPage(scope.row.F_RECEIVE_DOC_NUM )">
                        <span style="color:#66CDAA;cursor:pointer">
                            {{ scope.row.F_RECEIVE_DOC_NUM }}
                        </span>
                    </el-button>
                </template>
            </el-table-column>
            <el-table-column prop="APPLY_EMP" :label="$t('Mã thẻ người tạo đơn')"></el-table-column>
            <el-table-column prop="APPLY_NAME" :label="$t('Họ tên người tạo đơn')"></el-table-column>
            <el-table-column prop="F_RECEIVE_UNIT" :label="$t('Tên đơn vị xin cấp phát')"></el-table-column>
            <el-table-column prop="APPLY_TIME" :label="$t('Thời gian xin đơn')"></el-table-column>
            <el-table-column prop="F_STATUS" :label="$t('Trạng thái đơn')"></el-table-column>
            <el-table-column prop="F_SYSDATE" :label="$t('Ngày cập nhật')"></el-table-column>
            <el-table-column prop="F_SYSID" :label="$t('Người cập nhật')"></el-table-column>

        </el-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>

    </div>
</template>
<script>
import api from "@/model/api.js"
import { ElMessage } from 'element-plus'
export default
    {
        name: "SearchAppForIssuance",
        data() {
            return {
                Loading:false,
                tableData: [],
                tableKey: 0,
                search: '',
                currentPage: 1,
                pageSize: 10,
                total: 0,
            }

        },
        methods: {
            GotoDetailDocNumPage(ReceiveDocNum)
            {
                 this.$router.push({name:"NewApplicationForIssuance",query:{receiveDocNum:ReceiveDocNum}})
            },
            GetAllAppForIssuanceLs() {
                this.Loading=true;
                api.GetAllAppForIssuanceLs({
                    search: this.search,
                    page: this.currentPage,
                    pageSize: this.pageSize,
                },
                    (dat) => {
                        this.Loading=false;
                        if (dat.data.status == "error") {
                            ElMessage.error(dat?.data?.message)
                        }
                        else {
                            let temp = dat.data.data;
                            this.tableData = temp.items;
                            this.total = temp.totalCount;
                            this.tableKey += 1;

                        }
                    },
                    (er) => {
                        this.Loading=false;
                        ElMessage.error(er.message)
                    });
            },
            SearchData() {
                this.GetAllAppForIssuanceLs();
            },
            handleSizeChange(size) {
                this.pageSize = size;
                this.GetAllAppForIssuanceLs();
            },
            handleCurrentChange(page) {
                this.currentPage = page;
                this.GetAllAppForIssuanceLs();
            },


        },
        mounted() {
            this.GetAllAppForIssuanceLs();
        },


    }
</script>
<style scoped></style>